@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Noto Sans KR", sans-serif;
  overflow-x: hidden;
}
section {
  scroll-snap-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v7.0.29,
* Autoprefixer: v9.7.6
* Browsers: last 4 version
*/

.NavBar {
  width: 100%;
  height: 40px;
  background-color: rgba(47, 38, 38, 0.2);
  position: fixed;
  z-index: 90;
}
.NavBar .logo {
  position: absolute;
  width: 3rem;
  height: 100%;
  left: 1rem;
  overflow: hidden;
}

.NavBar .navigation {
  position: absolute;
  right: 0rem;
  width: 20rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

@media (max-width: 700px) {
  .NavBar .navigation {
    width: 3rem;
    right: 1rem;
  }
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v7.0.29,
* Autoprefixer: v9.7.6
* Browsers: last 4 version
*/

.navlinks {
  list-style: none;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 700px) {
  .navlinks {
    display: none;
  }
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v7.0.29,
* Autoprefixer: v9.7.6
* Browsers: last 4 version
*/

li a {
  text-decoration: none;
  color: #fff;
  margin-right: 2rem;
  margin-bottom: 2rem;
  text-align: center;
  transition: all 0.2s ease-in-out;
}

li a:hover,
li a:active,
li a.active {
  color: #2f2626;
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v7.0.29,
* Autoprefixer: v9.7.6
* Browsers: last 4 version
*/

.MobNav {
  display: none;
}

@media (max-width: 700px) {
  .MobNav {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .MobNav div {
    background-color: #fff;
    height: 20%;
    border-radius: 5px;
    width: 90%;
    margin-bottom: 0.3rem;
  }
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v7.0.29,
* Autoprefixer: v9.7.6
* Browsers: last 4 version
*/

.NavModal {
  display: none;
  padding: 0;
  margin: 0;
  z-index: 150;
}

@media (max-width: 700px) {
  .NavModal {
    padding-top: 2rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
    width: 50%;
    height: 100%;
    position: fixed;
    background-color: black;
    right: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.5);
    list-style: none;
  }
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v7.0.29,
* Autoprefixer: v9.7.6
* Browsers: last 4 version
*/

.BackDrop {
  width: 100%;
  height: 300vh;
  position: absolute;
  background-color: rgba(37, 8, 8, 0.8);
  opacity: 0.6;
  z-index: 100;
}
@media (max-width: 700px) {
  .BackDrop {
    width: 100%;
    height: 300vh;
    position: absolute;
    background-color: rgba(37, 8, 8, 0.8);
    opacity: 0.6;
    z-index: 100;
  }
}

.ExpandArrow {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
}

.ExpandArrow i {
  color: #bfccce;
  font-size: 30pt;
  cursor: pointer;
  -webkit-animation: animate 2s infinite linear;
  animation: animate 2s infinite linear;
}

.Caption {
  width: 60vh;
  line-height: 3.5rem;
  margin-left: 1.5rem;
}
.Caption h1,
.Caption h2,
.Caption h3 {
  margin: 0;
  text-shadow: 0.5rem 0.5rem 0.5rem rgba(0, 0, 0, 0.3);
}

.Caption div h3 {
  color: #ffffff;
  font-size: 3.8rem;
}

.Caption div h2 {
  font-size: 5rem;
  color: #f6ea68;
  line-height: 3.3rem;
}
.Caption div h1 {
  line-height: 5.8rem;
  font-size: 9rem;
  color: #ffa090;
}

@media (max-width: 700px) {
  .Caption {
    display: none;
  }
}

.MobCapion {
  display: none;
}
@media (max-width: 700px) {
  .MobCapion {
    display: block;
    width: 100%;
    text-align: center;
    margin-bottom: 5rem;
    margin-top: 3rem;
  }
  .MobCapion div h3 {
    font-size: 3rem;
    line-height: 2.2rem;
    color: #ffffff;
  }

  .MobCapion div h2 {
    color: #f6ea68;
    font-size: 4.4rem;
    line-height: 0.75;
  }
  .MobCapion div h1 {
    font-size: 6rem;
    line-height: 0.75;
    color: #ffa090;
  }
  .MobCapion h1,
  .MobCapion h2,
  .MobCapion h3 {
    margin: 0;
    text-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.4);
  }
}

.FirstSection {
  width: 100%;
  height: 100vh;
  display: flex;
  scroll-behavior: smooth;
}

.FirstSection .RighSide {
  background-color: rosybrown;
  height: 100%;
  width: 100%;
  background-image: url(/Professional-Gallery/static/media/rightside.395339e0.png);
  background-attachment: fixed;
  background-position: right;
  background-size: contain;
}

.FirstSection .LeftSide {
  height: 100%;
  width: 100%;
  background-image: url(/Professional-Gallery/static/media/leftside.78a60011.jpg);
  background-attachment: fixed;
  background-position: left;
  background-size: contain;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.Lens {
  position: absolute;
  width: 100%;
  top: 15vh;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Lens img {
  /* width: 50vh; */
  width: 19.875rem;
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

@media (max-width: 700px) {
  .FirstSection {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  .FirstSection .RighSide {
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .FirstSection .LeftSide {
    background-size: cover;
    justify-content: center;
    align-items: center;
  }
  .Lens {
    position: absolute;
    width: 100%;
    top: 30vh;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .Lens img {
    /* width: 30vh; */
    width: 11.90625rem;
  }
}

.SecondSection {
  height: 100vh;
  margin: 0;
  position: relative;
  background-image: url(/Professional-Gallery/static/media/bg.b5618891.jpg);
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}
.SecondSection .quote {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 2.5rem;
  color: #0a0a0a;
  background-color: white;
  opacity: 0.8;
  font-size: 1rem;
  z-index: 10;
}
.imges-container-grid {
  width: 90%;
  height: 80%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  position: absolute;
  overflow-y: scroll;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  top: 5.5rem;
  justify-content: center;
  align-items: flex-start;
  background-color: rgba(0, 0, 0, 0.7);
}

@media (max-width: 700px) {
  .imges-container-grid {
    width: 90%;
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
  }
}

.img-container {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

.img-container img {
  min-width: 80%;
  min-height: 80%;
  max-width: 120%;
}

.IMG-MODAL {
  width: 80%;
  height: 80%;
  position: absolute;
  top: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ivory;
  z-index: 600;
}
.IMG-MODAL img {
  max-width: 100%;
  max-height: 100%;
}

.about {
  width: 100%;
  height: 92vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 1vh;
}

.about .JacobVector {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 1rem;
}

.about .JacobVector img {
  width: 100%;
  display: inline-block;
}
.about .bio {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
.about .bio h1,
.about .bio p {
  text-align: start;
  width: 90%;
  font-family: "Noto Serif", serif;
  color: #5b5b5b;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}
.about .bio h1 {
  font-size: 3rem;
}

@media (max-width: 700px) {
  .about {
    flex-direction: column;
  }
  .about .JacobVector {
    width: 95%;
    overflow: visible;
    margin-top: 15rem;
  }
  .about .JacobVector img {
    width: 100%;
    border-radius: 1rem;
  }
  .about .bio {
    width: 100%;
    align-items: center;
  }
}

